        <Row class="expand-row">
            <Col span="8">
                <span class="expand-key">进价: </span>
                <span class="expand-value">{{ row.costPrice }}</span>
            </Col>
            <Col span="8">
                <span class="expand-key">净重: </span>
                <span class="expand-value">{{ row.nw }}</span>
            </Col>
        </Row>
