            <Col span="8">
                <span class="expand-key">所属类型: </span>
                <span class="expand-value">{{ row.typeId }}</span>
            </Col>
            <Col span="8">
                <span class="expand-key">描述: </span>
                <span class="expand-value">{{ row.description }}</span>
            </Col>
        </Row>
    </div>
